.SignUpWrapper{
  width: 100%;
  background: url('../../assets/img/wave.png') no-repeat;
  height: 100vh;
  background-size: 100% 60%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}

.formSingUp{
  width: 34%;
  max-width: 70%;
  border-radius: 24px;
  background: var(--ffffff, #FFF);
  display: flex;
  flex-direction: column;
  padding: 0 24px 24px 24px;
  align-items: center;
  gap: 1.5vh;
  margin-top: 5%;
}

@media screen and (max-width: 1024px) {
  .formSingUp {
    width: 50%; /* При уменьшении ширины экрана устанавливаем ширину до 80% */
  }
}

@media screen and (max-width: 425px) {
  .formSingUp {
    width: 70%; /* При уменьшении ширины экрана устанавливаем ширину до 80% */
  }
}

.logo{
  align-self: flex-start;
  margin-top: 4%;
  margin-left: 10%;
}

.centerBlock{
  width: 100%;
  height: 70%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.createAcc{
  margin-top: 1%;
}

.smContainer{
  display: flex;
  gap: 10px;
  margin-top: 2%;
}

.socialMediaBtn{
  cursor: pointer;
  display: flex;
  align-items: center;
  background: #FFFFFF;
  padding: 8px;
  gap: 7px;
  border-radius: 10px;
  border: 1px solid #B0B0B0;
}

.orLineContainer{
  display: flex;
  width: 34%;
  justify-content: center;
  align-items: center;
  color: #B0B0B0;
  margin-top: 2%;
  gap: 5px;
}

.orLine{
  height: 1px;
  background: #B0B0B0;
  width: 47%;
}