.headerWrapper{
  background: #30434A;
  position: sticky;
  z-index: 99;
  top: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 7%;
}

.logo{
  width: 120px;
}

@media screen and (max-width: 1024px) {
  .logo {
    width: 100px; /* При уменьшении ширины экрана устанавливаем ширину до 80% */
  }
}
@media screen and (max-width: 768px) {
  .logo {
    width: 80px; /* При уменьшении ширины экрана устанавливаем ширину до 80% */
  }
}

.menu{
  display: flex;
  gap: 40px;
}

.menuItem{
  display: flex;
  flex-direction: column;
  gap: 3px;
  align-items: center;
  cursor: pointer;
}

.icon{
  width: 32px;
  color: white;
  font-size: 32px;
}

.menuItem span{
  color: #FFFFFF;
  font-size: calc(10px + 4 * (100vw / 1280));
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}