.svgContainer {
  width: 100%;
  max-width: 200px; /* Можете изменить максимальную ширину по вашему усмотрению */
  margin: 0 auto;
}

svg {
  width: 100%;
  height: auto;
}

@media (max-width: 1024px) {
  .svgContainer {
    max-width: 150px; /* Пример: уменьшаем максимальную ширину для устройств с шириной экрана до 600px */
  }
}

@media (max-width: 768px) {
  .svgContainer {
    max-width: 120px; /* Пример: уменьшаем максимальную ширину для устройств с шириной экрана до 600px */
  }
}

@media (max-width: 425px) {
  .svgContainer {
    max-width: 120px; /* Пример: уменьшаем максимальную ширину для устройств с шириной экрана до 600px */
  }
}
