.avatarBoxWrapper {
 display: flex;
  flex-direction: column;
  background: white;
  padding: 24px;
  width: fit-content;
  border-radius: 10px;
  height: fit-content;
}

.avablock {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.avaImg{
  border-radius: 50%;
}

.avaText {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.avaText:nth-child(2) {
  color: #B0B0B0;
}

.avaText span:first-child {
  font-weight: bold;
  color: black;
}