.btn{
  display: flex;
  padding: 8px 12px;
  justify-content: center;
  align-items: flex-start;
  gap: 10px;
  align-self: stretch;
  border-radius: 50px;
  background: #386A7C;
  color: #ffffff;
  box-shadow: none;
  border: none;
  font-size: calc(10px + 4 * (100vw / 1280));
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  font-family: 'Asap', sans-serif;
  transition: box-shadow 0.3s ease; /* добавлен переход для анимации тени */
}

.btn:hover {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5); /* добавлена тень при наведении */
}