/* styles.css (или ваш файл стилей) */
/* Добавьте стили для подсветки при фокусе */
.customInput:focus {
  outline: none; /* Убираем дефолтную подсветку */
}

.customInputWrapper:focus-within {
  border: 1px solid #56a0bf; /* Добавляем кастомную подсветку при фокусе на вложенном input */
}

.customInputWrapper{
  border-radius: 9px;
  border: 1px solid #B0B0B0;
  background: #FFF;
  font-size: calc(12px + 4 * (100vw / 1280));
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  max-height: 45px;
}

.customInput::placeholder {
  color: #B0B0B0;
}

.customInput{
  margin: 10px;
  border: none;
  width: 95%;
  padding: 0;
  font-size: calc(12px + 4 * (100vw / 1280));
}

.label {
  font-size: calc(11px + 4 * (100vw / 1280));
  color: #B0B0B0;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}

.errorText{
  color: rgb(235, 6, 6);
  font-size: calc(8px + 4 * (100vw / 1280));
}