.App {
  text-align: center;
}

@font-face {
  font-family: "Asap";
  src: url("../fonts/Asap/static/Asap-Medium.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
}

@font-face {
  font-family: "AsapThin";
  src: url("../fonts/Asap/static/Asap-Thin.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
}

body, html{
  margin: 0;
  padding: 0;
}

body{
  background: var(--F4F2EE, #F4F2EE);
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  font-size: calc(10px + 5 * (100vw / 1280));
  font-family: 'AsapThin', sans-serif; /* Используйте имя шрифта, указанное в @font-face */
}

h2{
  font-family: "Asap", sans-serif;
  font-size: calc(20px + 16 * (100vw / 1280));
}

.link{
  color: #0966C2;
  cursor: pointer;
}

.loaderWrapper{
  width: 100%;
  height: 80vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.loader {
  position: relative;
  width: 120px;
  height: 140px;
  background-image: radial-gradient(circle 30px, #386A7C 100%, transparent 0),
  radial-gradient(circle 5px, #386A7C 100%, transparent 0),
  radial-gradient(circle 5px, #386A7C 100%, transparent 0),
  linear-gradient(#386A7C 20px, transparent 0);
  background-position: center 127px , 94px 102px , 16px 18px, center 114px;
  background-size: 60px 60px, 10px 10px , 10px 10px , 4px 14px;
  background-repeat: no-repeat;
  z-index: 10;
  perspective: 500px;
}
.loader::before {
  content: '';
  position: absolute;
  width: 100px;
  height: 100px;
  border-radius:50%;
  border: 3px solid #386A7C;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -55%) rotate(-45deg);
  border-right-color: transparent;
  box-sizing: border-box;
}
.loader::after {
  content: '';
  position: absolute;
  height: 80px;
  width: 80px;
  transform: translate(-50%, -55%) rotate(-45deg) rotateY(0deg) ;
  left: 50%;
  top: 50%;
  box-sizing: border-box;
  border: 7px solid #FF3D00;
  border-radius:50%;
  animation: rotate 0.5s linear infinite;
}

@keyframes rotate {
  to{transform: translate(-50%, -55%) rotate(-45deg) rotateY(360deg)   }
}
